<template>
  <div id="record-poem">
    <div class="poem-header" @click="clickLeft">
      <div class="poem-bgc">
        <img src="../../assets/imgs/images/返回.png" />
      </div>
      <h3>歌词详情</h3>
    </div>
    <div class="poem-content">
      <h2>{{ poemItemContent.title }}</h2>
      <p>作者:{{ poemItemContent.author_name }}</p>
      <div>
        <p v-for="(item, i) in poemText" :key="i">
          {{ item }}
        </p>
      </div>
    </div>
    <div class="poem-botton">
      <button @click="record">朗读</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "recordPoem",
  data() {
    return {
      poemItemContent: this.$route.query.item,
      poemText: [],
    };
  },
  created() {
    this.$nextTick(()=>{
      this.getData();
    })
  },
  methods: {
    clickLeft() {
      this.$router.back(-1);
    },
    getData() {
      this.poemText = this.poemItemContent.original.split("\n");
    },
    record() {
      // this.$router.push({
      //   path:"/record-actoin"
      // })
    },

  },
};
</script>

